import { TabPanel } from '@mui/lab'
import { Accordion, AccordionDetails, AccordionSummary, Avatar, Box, List, Typography } from '@mui/material'
import React from 'react'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';



const NewTesment = () => {
    const [data,setData]=React.useState(
        [
          {
            id:1,
            name:"jhon",
            sfr:['1','2','3','4']
    
          },
          {
            id:2,
            name:"mathew",
            sfr:['1','2']
    
          },
          {
            id:3,
            name:"luk",
            sfr:['1','2','5']
    
          },
        ]
      )
    
  return (
    <Box>
    <TabPanel value="1">
    <List>
      {
     data.map((item)=>(
      <Accordion key={item.id}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1a-content"
        id="panel1a-header"
      >
        <Typography>{item.name}</Typography>
      </AccordionSummary>
      <AccordionDetails sx={{display:'flex', flexWrap:'wrap'}}>
        {
          item.sfr?.map((item)=>(
            <Box sx={{margin:'3px'}}>
              <Avatar sx={{ width: 30, height: 30 ,bgcolor: '#795548' }}>
                <Typography variant="body2" >{item}</Typography>
                </Avatar>
          
        
        </Box>
          ))
        }
      </AccordionDetails>
  </Accordion>
     ))
      }        
      </List>
    </TabPanel>
  
    </Box>
  
  )
}

export default NewTesment