
import { TabPanel } from '@mui/lab'
import { Accordion, AccordionDetails, AccordionSummary, Avatar, Box, List, Typography } from '@mui/material'
import React from 'react'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

interface Idata{
    id:number;
    name:string;
    sfr:string[]
    chapter:string[]

}
interface Props {
data:Idata[]
// HandelChapter: (e: any) => void;
}

const OldTesment:React.FC<Props>= ({data}) => {

   

  return (
    <Box>
    <TabPanel value="2">
    <List>
    {
     data.map((item)=>(
      <Accordion key={item.id}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1a-content"
        id="panel1a-header"
      >
        <Typography >{item.name}</Typography>
      </AccordionSummary>
      <AccordionDetails sx={{display:'flex', flexWrap:'wrap'}}>
        {
          item.sfr?.map((item,id)=>(
            <Box key={id} sx={{margin:'3px'}}>
              <Avatar sx={{ width: 30, height: 30 ,bgcolor: '#795548' }}>
                <Typography variant="body2" >{item}</Typography>
                </Avatar>
          
        
        </Box>
          ))
        }
      </AccordionDetails>
  </Accordion>
     ))
      }        
          </List>
    </TabPanel>
  
    </Box>
  
  )
}

export default OldTesment