import * as React from 'react';
import Backdrop from '@mui/material/Backdrop';
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import CssBaseline from '@mui/material/CssBaseline';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import InputLabel from '@mui/material/InputLabel';
import OutlinedInput from '@mui/material/OutlinedInput';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { Slider, Typography } from '@mui/material';

  // TODO remove, this demo shouldn't need to reset the theme.
  const defaultTheme = createTheme();
  

const style = {
    position: 'absolute' as 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    display:'flex',
    flexDirection:"column",
    textAlign: 'center',
    justifyContent: 'center',
    width: 400,
   
    bgcolor:'white',
    boxShadow: 24,
    p: 4,
  };
  
  interface props{
    handleSettingClose:()=>void;
    settingsOpen:boolean ;
  }

const Settings:React.FC <props>= ({settingsOpen,handleSettingClose}) => {
   
    const [open, setOpen] = React.useState(false);
    const [age, setAge] = React.useState<number | string>('');

    const handleChange = (event: SelectChangeEvent<typeof age>) => {
        setAge(Number(event.target.value) || '');
      };
    
      
      
      
  return (
      <Container component="main" maxWidth="md" sx={{width:'300px'}}>
        <CssBaseline />


          <Dialog disableEscapeKeyDown open={settingsOpen} onClose={handleSettingClose}>
        <DialogTitle>Settings</DialogTitle>
        <DialogContent >
          <Box component="form" sx={{ display: 'flex', flexWrap: 'wrap' ,width:'300px'}}>
            <FormControl sx={{ m: 1, width: '100%' }}>
            <Typography sx={{ m: '10px 0' }}>Bible Version</Typography>
              <Select
                labelId="demo-dialog-select-label"
                id="demo-dialog-select"
                value={age}
                onChange={handleChange}
              >
                <MenuItem value={10}>
                Louis Segond 1910
                </MenuItem>
                <MenuItem value={20}>
                  NKJV</MenuItem>
                <MenuItem value={30}>Arabic</MenuItem>
                <MenuItem value={30}>
                Riveduta 1927 (RIV)
                </MenuItem>
                <MenuItem value={30}>Coptic Bible</MenuItem>
              </Select>
            </FormControl>
            <FormControl sx={{ m: 1, width: '100%' }}>
            <Typography sx={{ m: '10px 0' }}>App Language</Typography>
              <Select
                labelId="demo-dialog-select-label"
                id="demo-dialog-select"
                value={age}
                onChange={handleChange}
              >
                <MenuItem value={10}>English</MenuItem>
                <MenuItem value={20}>Arabic</MenuItem>
                <MenuItem value={30}>French</MenuItem>
                <MenuItem value={30}>Italian</MenuItem>
              </Select>
            </FormControl>
            <FormControl sx={{ m: 1, width: '100%' }}>
            <Typography sx={{ m: '10px 0' }}>Font Size</Typography>
              <Select
                labelId="demo-dialog-select-label"
                id="demo-dialog-select"
                value={age}
                onChange={handleChange}
              >
                <MenuItem value={10}>Small</MenuItem>
                <MenuItem value={20}>Medium</MenuItem>
                <MenuItem value={30}>Large</MenuItem>
                <MenuItem value={30}>X-Large</MenuItem>
              </Select>
          
            </FormControl>

          </Box>
        </DialogContent>
        <DialogActions>
          <Button sx={{color:'black'}} onClick={handleSettingClose}>Cancel</Button>
          <Button sx={{color:'black'}} onClick={handleSettingClose}>Ok</Button>
        </DialogActions>
      </Dialog>

       
      </Container>
  );
}
  


export default Settings